<template>
	<view>
		<Search></Search>

		<view class="bg-body-secondary">
			<uni-row>
				<uni-col :span="6">
					<view class="bg-body-tertiary text-center" style="height: calc(100vh - 56px);">
						<view class="p-2" v-for="(item, index) in items" :key="'taxonomy' + index">热门</view>
						<view :class="['p-2', selectIndex == index ? ' bg-body-secondary fw-bold' : '']">{{item.name}}</view>
					</view>
				</uni-col>
				<uni-col :span="18">
					<view class="px-2">
						<view class="d-flex justify-content-start align-items-center bg-body-tertiary rounded-2 p-2 mt-2">
							<image style="width: 50px; height: 50px;" mode="heightFix" src="https://qiniu-web-assets.dcloud.net.cn/unidoc/zh/unicloudlogo.png"></image>
							<view class="ms-2">
								<view class="fw-bold f16">列表左侧带略缩图</view>
								<view class="text-body-secondary mt-1 f12">列表描述信息</view>
							</view>
						</view>
					</view>
				</uni-col>
			</uni-row>
		</view>
	</view>
</template>

<script>
	import {mapActions} from 'pinia'
	import {taxonomyStore} from '@/stores/taxonomy'
	import Search from '@/components/search';
	export default {
		components: {
			Search
		},
		data() {
			return {
				items: [],
				selectIndex: 0,
			}
		},
		onLoad() {
			this.fetch()
		},
		onPullDownRefresh() {
			this.fetch()
		},
		methods: {
			...mapActions(taxonomyStore, ['taxonomy']),
			fetch() {
				this.taxonomy().then(resp => {
					this.items = resp.data
					console.log('items', this.items)
					uni.stopPullDownRefresh()
				})
			}
		}
	}
</script>

